body {
  background-color: azure;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  gap: 20px;
  margin: 10px;
  display: flex;
}

.breed-select {
  display: inline-flex;
}

img {
  border: 1px solid #000;
}

.loader {
  width: 64px;
  height: 64px;
  background-color: rgba(0, 0, 0, .5);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  background: azure;
  margin: auto;
  position: absolute;
  top: 8px;
  bottom: 8px;
  left: 8px;
  right: 8px;
}

.loader:before {
  content: "";
  background: #de3500;
  margin: auto;
  animation: diamondLoader 2s linear infinite;
  position: absolute;
  top: -15px;
  bottom: -15px;
  left: -15px;
  right: -15px;
}

@keyframes diamondLoader {
  0%, 10% {
    transform: translate(-64px, -64px)rotate(-45deg);
  }

  90%, 100% {
    transform: translate(0)rotate(-45deg);
  }
}

